body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/* Flex */
.flex {
	display: flex;
}

.flex-wrap {
	flex-wrap: wrap;
}

/* Flex Item */
.item {
	margin: 5px;
    text-align: center;
	border: 1px solid red;
	padding: 10px;
}

.container {
	max-width: 400px;
	margin: 0 auto;
	border: 1px solid #ccc;
}
h1 {
	text-align: center;
	margin: 20px 0 0 0;
	font-size: 1.25em;
	font-weight: normal;
}
body{
    padding:0;
    margin: 15px;
    font-size: 20px;
    background-color: #fff;
}

ul {
	list-style-type:none;
}
.button-component{
    border-radius: 30px;
    border:none;
